import axios from 'axios';
import localEvent from 'store';
import router from './routers/';
import Message from '@/plugins/message';
import { plusMessageAnalyze } from '@/filters';
/**
 * 
 *     @author jsonleex <jsonlseex@163.com>
 */
let TOKEN;
axios.interceptors.request.use(
  config => {
    TOKEN = (localEvent.get('CURRENTUSER') || {}).token;

    // if(config.method === 'post') {
    //     // JSON  FormData
    //     const formData = new FormData()
    //     Object.keys(config.data).forEach(key => formData.append(key, config.data[key]))
    //     config.data = formData
    // }

    if (TOKEN) {
      config.headers.Authorization = `Bearer ${TOKEN}`;
    }
    return config;
  },
  error => {
    console.error('', 'fail');
    return Promise.reject(error);
  }
);

/**
 * 
 *     @author jsonleex <jsonlseex@163.com>
 */
axios.interceptors.response.use(
  res => res,
  /*  */
  error => {
    if (error.response) {
      const { status, data } = error.response;
      switch (status) {
        case 401:
          localEvent.remove('CURRENTUSER');
          setTimeout(() => {
            router.push({
              path: '/signin',
              query: { redirect: router.currentRoute.fullPath }
            });
          }, 500);
          const message401 = TOKEN ? ', ' : '';
          Message.error(message401);
          break;
        default:
          const message = plusMessageAnalyze(data);
          if (message) {
            Message.error(message);
          }
          break;
      }
    } else if (error.request) {
      console.log(error.request);
    } else {
      console.log('Error', error.message);
    }
    return false;
  }
);

/*  api */
const basename = document.head.querySelector('meta[name="api-basename"]');
if (!basename) {
  console.error(
    'api "<meta name="api-basename" content="url">"'
  );
}

//  v2 
axios.defaults.baseURL = basename.content || '/api/v2';

export default axios;



