import Vue from 'vue';
import Vuex from 'vuex';
import LocalEvent from 'store';
import actions from './action';
import getters from './getters';
import mutations from './mutations';
import { detectOS } from '../util/';
import http from '../http';

import modules from './module/';

Vue.use(Vuex);

async function bootstrappers() {
  const { data = {} } = await http.get('/bootstrappers');
  LocalEvent.set('CONFIG', data || {});
  return data;
}

const state = {
  CONFIG: Object.assign({}, LocalEvent.get('CONFIG'), bootstrappers()),
  /*  */
  BROWSER: detectOS(),
  /*  type */
  FEEDTYPE: '',
  /*  */
  USERTAGS: LocalEvent.get('USERTAGS') || [],
  /*  */
  GROUPTYPES: LocalEvent.get('GROUPTYPES') || [],
  /*  */
  CURRENTUSER: LocalEvent.get('CURRENTUSER') || {},

  /*  */
  SEARCHHISTORY: LocalEvent.get('SEARCHHISTORY') || [],

  /*  */
  LOCATION: LocalEvent.get('LOCATION') || {},
  /*  */
  HOTCTIYS: LocalEvent.get('HOTCTIYS') || [],
  /*   */
  CUR_SELECTED_TAGS: [],
  /*   */
  CUR_GROUP_LOCATION: {}
};

export default new Vuex.Store({
  state,
  getters,
  actions,
  mutations,
  modules
});



