/**
 * 
 *     @author jsonleex <jsonlseex@163.com>
 */
export const str2Hump = (str) => {
  return str.replace(/-(\w)/g, ($0, $1) => {
    return $1.toUpperCase()
  })
}

/**
 * 
 *     @author jsonleex <jsonlseex@163.com>
 */
export const oneOf = (value, validList) => {
  return validList.indexOf(value) > -1
}

/**
 * 
 *     @author jsonleex <jsonlseex@163.com>
 */
export const getStyle = (element, attr, NumberMode = 'int') => {
  let target
  if (attr === 'scrollTop') {
    target = getScrollTop(element)
  } else if (element.currentStyle) {
    target = element.currentStyle[attr]
  } else {
    target = document.defaultView.getComputedStyle(element, null)[attr]
  }
  //  opactiy  parseFloat
  return NumberMode === 'float' ? parseFloat(target) : parseInt(target)
}

/**
 *  ScrollTop
 *   @author jsonleex <jsonlseex@163.com>
 */
export const getScrollTop = (element) => {
  if (element === window) {
    return Math.max(window.pageYOffset || 0, document.documentElement.scrollTop)
  }

  return element.scrollTop
}

/**
 *  
 *   @author jsonleex <jsonlseex@163.com>
 */
export const getScrollEventTarget = (element) => {
  let currentNode = element
  while (
    currentNode &&
    currentNode.tagName !== 'HTML' &&
    currentNode.tagName !== 'BODY' &&
    currentNode.nodeType === 1
  ) {
    let overflowY = document.defaultView.getComputedStyle(currentNode).overflowY
    if (overflowY === 'scroll' || overflowY === 'auto') {
      return currentNode
    }
    currentNode = currentNode.parentNode
  }
  return window
}

/**
 * 
 *     @author jsonleex <jsonlseex@163.com>
 */
export const detectOS = () => {
  const u = navigator.userAgent

  /* eslint-disable one-var */
  const
    isMobile = !!u.match(/AppleWebKit.*Mobile.*/),

    /*  */
    isIPad = u.indexOf('iPad') > -1,
    isIPhone = u.indexOf('iPhone') > -1,
    isWebApp = u.indexOf('Safari') === -1,
    isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/),
    isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1,

    /* pc  */
    isIE = u.indexOf('Trident') > -1,
    isOpera = u.indexOf('Presto') > -1,
    isChorme = u.indexOf('AppleWebKit') > -1,
    isFirefix = u.indexOf('Gecko') > -1 && u.indexOf('KHTML') === -1
  return {
    isMobile,
    OS: (() => {
      if (isMobile) {
        if (isIOS) return 'IOS'
        if (isIPad) return 'iPad'
        if (isIPhone) return 'IOS'
        if (isAndroid) return 'Android'
      } else {
        if (isIE) return 'IE'
        if (isOpera) return 'Opera'
        if (isChorme) return 'Chorme'
        if (isFirefix) return 'Firefix'
      }
    })(),
    versions: (() => {
      return {
        /*  */
        mobile: isMobile,

        /* iPhoneQQHD */
        iPhone: isIPhone,
        /* androiduc */
        android: isAndroid,

        /* ios */
        ios: isIOS,
        /* iPad */
        iPad: isIPad,

        /*  web  */
        webApp: isWebApp,

        /* IE */
        trident: isIE,
        /* opera */
        presto: isOpera,
        /*  */
        webKit: isChorme,
        /*  */
        gecko: isFirefix
      }
    })(),
    language: (navigator.browserLanguage || navigator.language).toLowerCase()
  }
}

/**
 * 
 *     @author jsonleex <jsonlseex@163.com>
 */
export const formatNum = (num) => {
  if (typeof ~~(num) === 'number') {
    if (num === 0) return '0'
    var k = 1000,
      sizes = ['', 'K', 'M', 'G', 'T', 'P', 'E', 'Z', 'Y'],
      i = Math.floor(Math.log(num) / Math.log(k))

    return (num / Math.pow(k, i)).toPrecision(3) + ' ' + sizes[i]
  }
  return '0'
}

/**
 * create object url
 *     @author jsonleex <jsonlseex@163.com>
 */
export const getFileUrl = (file) => {
  let url = null
  if (window.createObjectURL !== undefined) { // basic
    url = window.createObjectURL(file)
  } else if (window.URL !== undefined) { // mozilla(firefox)
    url = window.URL.createObjectURL(file)
  } else if (window.webkitURL !== undefined) { // webkit or chrome
    url = window.webkitURL.createObjectURL(file)
  }
  return url
}



