function test () {}
test()
export default (Vue, options = {}) => {
  // item remove
  /* eslint-disable no-extend-native */
  if (!Array.prototype.remove) {
    Array.prototype.remove = function (item) {
      if (!this.length) return
      var index = this.indexOf(item)
      if (index > -1) {
        this.splice(index, 1)
        return this
      }
    }
  }
  // 
  const { offset, placeholeder } = options
  // 
  const settings = {
    offset: offset || 70,
    lazyLoad: false,
    default: placeholeder || 'data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAQQAAAEEAQMAAAD0xthJAAAAA1BMVEXMzMzKUkQnAAAAH0lEQVRo3u3BMQEAAADCIPuntsROYAAAAAAAAAAAEDgiiAAB2NF59gAAAABJRU5ErkJggg=='
  }
  const listenList = []
  const imageCatcheList = []

  const isAlredyLoad = (imageSrc) => {
    if (imageCatcheList.indexOf(imageSrc) > -1) {
      return true
    } else {
      return false
    }
  }
  // 
  const isCanShow = (item) => {
    const ele = item.ele
    const src = item.src
    // const windowHeight = window.innerHight

    // 
    const top = ele.getBoundingClientRect().top

    // offset  70
    if (top + settings.offset < window.innerHeight) {
      const image = new Image()
      image.src = src
      image.onload = () => {
        ele.src = src
        const s = image.naturalHeight / image.naturalWidth
        if (s > 3 || s < 0.3) {
          ele.parentNode.classList.add('long')
        }
        imageCatcheList.push(src)
        listenList.remove(item)
      }
      image.onerror = () => {
        ele.src = settings.default
        listenList.remove(item)
      }
      return true
    } else {
      return false
    }
  }

  const onListenScroll = () => {
    window.addEventListener('scroll', () => {
      const length = listenList.length
      for (let i = 0; i < length; i++) {
        isCanShow(listenList[i])
      }
    })
  }
  // Vue 
  const addListener = (ele, binding) => {
    // 
    const imageSrc = binding.value
    // src
    if (isAlredyLoad(imageSrc)) {
      ele.src = imageSrc
      return false
    }
    const item = {
      ele: ele,
      src: imageSrc
    }
    // 
    ele.src = settings.default
    // 
    if (isCanShow(item)) {
      return
    }
    // lisenList
    listenList.push(item)

    // scroll
    onListenScroll()
  }

  Vue.directive('lazyload', {
    inserted: addListener,
    updated: addListener
  })
}



